<template>
  <div class="strategy">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>策略管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-button type="primary" @click="openAdd">添加策略</el-button>
      </el-row>
      <el-table :data="userList" stripe :row-key="getRowKeys"
                :expand-row-keys="expands" @expand-change="expandChange">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-row v-if="props.row.ruleItemData.length > 0">
              <span style="display:inline-block;margin:5px;" v-for="item in props.row.ruleItemData"
                    :key="item.apiId">
              <el-tag closable @close="removeRightById(props.row, item.strategyActionId)">{{item.apiName}}</el-tag>
            </span>
            </el-row>
            <el-row v-else>
              暂无配置操作
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index" label="序号"></el-table-column>
        <el-table-column label="策略名称" prop="strategyName" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="描述" prop="strategyDescribe" :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <span>{{scope.row.strategyDescribe === null || scope.row.strategyDescribe === '' ? '-' : scope.row.strategyDescribe}}</span>
          </template>
        </el-table-column>
        <el-table-column label="创建时间" prop="createTime"></el-table-column>
        <el-table-column label="操作" width="280px">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="editById(scope.row)">编辑</el-button>
            <el-button size="mini" type="danger" @click="deleteById(scope.row.strategyId)">删除</el-button>
            <el-button size="mini" type="success" @click="openAction(scope.row)">添加操作</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5,10,15,20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
    <el-dialog :title="title" :visible.sync="addFormDialogVisible" width="500px" @close="addDialogClosed">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-form-item label="策略名称" prop="strategyName">
          <el-input maxlength="32" show-word-limit v-model.trim="addForm.strategyName"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="strategyDescribe">
          <el-input type="textarea" maxlength="128" show-word-limit :rows="8" v-model.trim="addForm.strategyDescribe"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addFormDialogVisible = false">取 消</el-button>
        <el-button type="primary"  v-preventReClick="1000" @click="addEnsure">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="添加操作" :visible.sync="actionDialogVisible" width="600px" @close="actionDialogClosed">
      <el-form :model="actionForm" :rules="actionFormRules" ref="actionFormRef" label-width="90px">
        <el-form-item label="选择操作:" prop="checkedAuth">
          <div class="block">
            <el-cascader style="width: 100%"
                         v-model="actionForm.checkedAuth"
                         :options="options"
                         :props="prop"
                         :show-all-levels="false"
                         clearable
            ></el-cascader>
          </div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="actionDialogVisible = false">取 消</el-button>
        <el-button type="primary"  v-preventReClick="1000" @click="submitAction">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        getRowKeys(row) {
          return row.strategyId
        },
        expands: [],
        userList: [],
        queryInfo: {
          pagenum: 1,
          pagesize: 10
        },
        total: 0,
        title: '',
        editId: null,
        addFormDialogVisible: false,
        addForm: {
          strategyName: '',
          strategyDescribe: '',
        },
        addFormRules: {
          strategyName: [{required: true, message: '必填项不能为空', trigger: 'blur'}, {
            min: 1,
            max: 32,
            message: '长度不能超过32个字符',
            trigger: 'blur'
          }],
          strategyDescribe: [{min: 0, max: 128, message: '长度不能超过128个字符', trigger: 'blur'}],
        },
//        添加操作
        actionStrategyId: '',
        actionDialogVisible: false,
        checkactionCode: [],
        actionForm: {
          checkedAuth: [],
        },
        actionFormRules: {},
        options: [],
        prop: {
          multiple: true,
          emitPath: false,
          value: 'apiId',
          label: 'apiName',
          children: 'apiResources',
        },
      }
    },
    beforeRouteEnter: (to, form, next) => {
      const loginType = window.sessionStorage.getItem('accountTypeLogin');
      if (loginType === '9') {
        next()
      }else{
        return next('/404')
      }
    },
    created() {
      this.getUserList();
    },
    methods: {
      async getUserList() {
        const {data: res} = await this.$http.get(`strategy/getStrategy/${this.queryInfo.pagenum}/${this.queryInfo.pagesize}?ts=${new Date().getTime()}`);
        if (res.code !== 0) return this.$message.error('获取策略信息失败！');
        //给每行数据强制追加一个数据项
        res.data.list.map(item => {
          item.ruleItemData = [];
        });
        this.userList = res.data.list;
        this.total = res.data.total;
      },
      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize;
        this.getUserList()
      },
      handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage;
        this.getUserList()
      },
      async expandChange(row, expandedRows) {
        this.expands = [];
        if (expandedRows.length > 0) {//展开
          row ? this.expands.push(row.strategyId) : '';
          this.getAuthority(row.strategyId);
        }
      },
      async getAuthority(strategyId) {//获取指定用户已添加的操作
        const {data: res} = await this.$http.get(`strategy/getActionInfo?strategyId=${strategyId}&ts=${new Date().getTime()}`);
        if (res.code !== 0) return this.$message.error((res.msg !== '' && res.msg !== null) ? res.msg : '获取操作失败！');
        this.userList.forEach((temp, index) => {
          if (temp.strategyId === strategyId) {
            this.userList[index].ruleItemData = res.data;
          }
        });
      },
      async removeRightById(row, strategyActionId) {//根据strategyActionId删除对应的操作
        const confirmResult = await
          this.$confirm(
            '此操作将删除该操作, 是否继续?',
            '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }
          ).catch(err => err);
        if (confirmResult !== 'confirm') return;
        const {data: res} = await
          this.$http.post(`strategy/deleteAction?strategyActionId=${strategyActionId}`);
        if (res.code !== 0) return this.$message.error(res.msg !== null && res.msg !== '' ? res.msg : '删除操作失败！');
        this.$message.success('删除操作成功！');
        this.getAuthority(row.strategyId);
      },
      async deleteById(id) {
        const confirmDelete = await this.$confirm(
          "此操作将删除该策略，是否继续？",
          "提示",
          {
            confimrButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          }
        ).catch(err => err);
        if (confirmDelete !== "confirm") return;
        const {data: res} = await this.$http.post(`strategy/deleteStrategy?strategyId=${id}`);
        if (res.code !== 0) return this.$message.error(res.msg !== '' && res.msg !== null ? res.msg : '删除策略失败！');
        this.$message.success('删除策略成功！');
        this.getUserList()
      },
      openAdd() {
        this.title = '添加策略';
        this.addFormDialogVisible = true;
      },
      addDialogClosed() {
        this.$refs.addFormRef.resetFields();
      },
      addEnsure() {
        this.$refs.addFormRef.validate(async valid => {
          if (!valid) return;
          let reqData;
          let reqUrl;
          if (this.title === '添加策略') {
            reqData = {
              'strategyName': this.addForm.strategyName,
              'strategyDescribe': this.addForm.strategyDescribe,
            };
            reqUrl = `strategy/addStrategy`
          } else {
            reqData = {
              'strategyName': this.addForm.strategyName,
              'strategyDescribe': this.addForm.strategyDescribe,
              'strategyId': this.editId,
            };
            reqUrl = `strategy/updateStrategy`
          }
          const alertMsg = this.title === '添加策略' ? '添加' : '编辑';
          const {data: res} = await this.$http.post(reqUrl, reqData);
          if (res.code !== 0) return this.$message.error(res.msg !== null && res.msg !== '' ? res.msg : alertMsg + '失败！');
          this.$message.success(alertMsg + '成功！');
          this.addFormDialogVisible = false;
          this.getUserList();
        })
      },
      editById(row) {
        this.editId = row.strategyId;
        this.addForm.strategyName = row.strategyName;
        this.addForm.strategyDescribe = row.strategyDescribe;
        this.title = '编辑策略';
        this.addFormDialogVisible = true;
      },
      async openAction(row) {
        this.actionStrategyId = row.strategyId;
        const {data: res} = await this.$http.get(`strategy/getActionInfo?strategyId=${row.strategyId}&ts=${new Date().getTime()}`);//获取指定用户已添加的操作
        if (res.code !== 0) return this.$message.error((res.msg !== '' && res.msg !== null) ? res.msg : '获取操作失败！');
        const huiXianCheck = res.data;
        if (huiXianCheck.length > 0) {
          for (let i = 0; i < huiXianCheck.length; i++) {
            this.checkactionCode.push(huiXianCheck[i].apiId);//拿到选中的权限，进行回显
          }
          this.actionForm.checkedAuth = [].concat(this.checkactionCode);
        }
        this.actionDialogVisible = true;
        this.getCategory()
      },
      actionDialogClosed() {
        this.$refs.actionFormRef.clearValidate();
        this.actionForm.checkedAuth = [];
        this.checkactionCode = [];
      },
      submitAction() {
        if (this.actionForm.checkedAuth.length < 1) return this.$message.error('请选择操作！');
        this.$refs.actionFormRef.validate(async valid => {
          if (!valid) return;
          let reqData = {
            "apiId": this.actionForm.checkedAuth,
            "strategyId": this.actionStrategyId
          };
          const {data: res} = await this.$http.post('strategy/addAction', reqData);
          if (res.code !== 0) return this.$message.error(res.msg !== null && res.msg !== '' ? res.msg : '添加操作失败！');
          this.$message.success('添加操作成功！');
          this.actionDialogVisible = false;
          this.getAuthority(this.actionStrategyId);
        })
      },
      async getCategory() {//一次性获取接口资源信息
        const {data: res} = await this.$http.post('resource/getResource');
        if (res.code !== 0) return this.$message.error('获取操作信息失败！');
        this.options = res.data;
      },
    }
  }
</script>
<style lang="less">
  .strategy {
    min-width: 1000px;
    .supSpan {
      display: inline-block;
      width: 84px;
      box-sizing: border-box;
      padding-right: 12px;
      text-align: right
    }
    .supSpan:before {
      content: '*';
      color: #F56C6C;
      margin-right: 4px;
    }
    .el-cascader__tags .el-tag > span {
      flex: auto;
    }
    .el-cascader-node__postfix {
      top: 50%;
      transform: translateY(-50%);
    }
  }


</style>
